.chevron {
  color: var(--primaryDkBlue);
}

.chevron::after {
	border-style: solid;
	border-width: 0.25em 0.25em 0 0;
	content: '';
	display: inline-block;
	height: rem(10);
	left: 0.15em;
	position: relative;
	top: 0.15em;
	transform: rotate(-45deg);
	vertical-align: top;
	width: rem(10);
}

.chevron.bottom:after {
	top: rem(5);
	transform: rotate(135deg);

  @include breakpoint-down(medium) {
    transform: rotate(none);
  }
}